import hFormAlert from '@/components/h-form-alert/h-form-alert.vue'
export default {
	components: {
		hFormAlert
	},
	data() {
		return {
			isVerify: 0,
			modal6: false,
		}
	},
	methods: {
		confirm(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了确定"
			})
		},
		cancel(e){
			this.hide6()
			uni.showToast({
				icon:"none",
				title:"你点击了取消"
			})
		},
		show6() {
			this.modal6 = true
		},
		hide6() {
			this.modal6 = false
		},
		modifyPassword(e){
			uni.navigateTo({
				url:"../modify-paaword/modify-paaword"
			})
		},
		feedback(e){
			uni.navigateTo({
				url:"../feedback/feedback"
			})
		},
		verify(e){
			uni.navigateTo({
				url:"../verify/verify"
			})
		}
	}
}
